/*
Chat css
*/
.chat {
  float: right;
  position: relative;
  width: 81%;
  height: 100vh;
  background: #20232B;
  color: #fff;

  .chat-header {
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1.5px solid #393942;
    .chatting-with {
      margin-left: 10px;
      display: flex;
      flex-direction: column;
      .online-status {
        font-size: .7em;
        color: #83848F;
        &::before {
          content: '';
          background-color: #31cd31;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          display: inline-block;
          margin-right: 5px;
        }
      }
    }
  }

  .conversation {
    height: calc(100vh - 8vw);
    overflow: auto;
    margin-top: 5px;
    ul {
      li {
        display: block;
        clear: both;
        margin: 15px 15px 5px 15px;
        font-size: 0.9em;
        p {
          display: inline-block;
          padding: 10px 15px;
          border-radius: 10px;
          &.sent {
            float: right;
            text-align: left;
            background: #17a2b8;

          }
          &.reply {
            float: left;
            text-align: right;
            background: #435f7a;
          }
        }
      }
    }
  }

  .msg-form {
    position: absolute;
    bottom: 10px;
    width: 100%;
    border: none;
    height: 50px;
    margin-top: 5px;
    font-size: 1.2em;
    color: #32465a;
    input {
      background: none !important;
      display: block;
      outline: none;
      border: 1px solid #9293a0;
      border-radius: 4px;
      height: 45px;
      color: #fff;
      margin: 0 auto;
    }
  }

  .no-chat-selected {
    position: absolute;
    top: 50%;
    left: 40%;
    padding: 10px;
    text-align: center;
    vertical-align: middle;
    background: #2c2c35;
    border-radius: 50px;
    width: 300px;
    font-size: 0.9em;
  }
}
